@import '../../styles/colors.scss';
table{
  display: inline-block;
}
.container{
  height: 80vh;
  width: 100%;
  overflow-y: auto;
}
.header{
  height: 5.4rem;
  background-color: $grey;
  color: $white;
  justify-content: space-around;
  align-content: center;
  border: 0;
  font-size: 1.4rem;
  font-weight: 600;
};

th{
  cursor: pointer;
}
.content{  
    height: 8.7rem;
      td{
        padding: 1.1rem;
        border: 1px solid $borderColor;
        font-size: 1.3rem;
        };
        td:nth-child(1){
          text-align: center;
        }
}

.table{
  display: flex;
  height: 5.4rem;
  font-size: 1.4rem;
  align-items: center;
};

.column__id{
 min-width: 11rem;
};

.column__title{
  min-width: 52.9rem;

};

.column__body{
width: 100%;
}

.arrow{
  transition: transform .3s;
  margin-left: 2.5rem;
    &__down{
      margin-left: 2.5rem;
      transition: transform .3s;
      transform: rotate(180deg);
  }
}
.notFound{
  padding: 10rem 10rem;
  font-size: 2.4rem;
  text-align: center;
}